import * as React from 'react';
import './index.scss';

interface ILoginState {
}

import Draw from './canvas-bg2';
export default class User extends React.Component <ILoginState> {
  public showBg: any;

  constructor(props: any) {
    super(props);
    this.state = {
    };
  }

  public componentDidMount () {
    const draw = new Draw(this.showBg);
    draw.start();
  }

  public render() {
    return (
      <section className="login">
        <section className="login-box">
          <p>login</p>
        </section>
        <canvas className="loginFun" ref={(canvas) => { this.showBg = canvas; }} />
      </section>
    );
  }
}


