interface Ipoint {
  x: number;
  y: number;
  time: number;
}

interface Ioptions {
  i?: any;
  x?: number;
  y?: number;
  w?: number;
  h?: number;
}


class Draw {
  protected dom: any;
  protected domImg: any;
  protected ctx: any;
  protected ctxImg: any;
  // 痕迹保留时间
  protected time: number;
  protected width: number;
  protected height: number;
  protected dpr: number;
  protected points:Ipoint[];
  protected options: Ioptions;
  constructor(dom: any, time = 1000) {
    this.dom = dom;
    this.ctx = dom.getContext('2d');
    this.points = [];
    this.time = time;
    this.domImg = document.createElement('canvas');
    this.ctxImg = this.domImg.getContext('2d');
  }

  public start () {
    window.onresize = () => {
      this.render();
    };
    document.addEventListener('mousemove', () => {
      this.move(event);
    });

    document.addEventListener('click', () => {
      this.move(event);
    });
    this.render();
  }

  public render () {
    this.width = document.documentElement.clientWidth || document.body.clientWidth;
    this.height = document.documentElement.clientHeight || document.body.clientHeight;
    this.dpr = window.devicePixelRatio;
    // 设置canvas宽高
    this.dom.style.width = this.width + 'px';
    this.dom.style.height = this.height + 'px';

    this.dom.width = this.width * this.dpr;
    this.dom.height = this.height * this.dpr;

    this.domImg.style.width = this.width + 'px';
    this.domImg.style.height = this.height + 'px';

    this.domImg.width = this.width * this.dpr;
    this.domImg.height = this.height * this.dpr;


    this.ctx.fillStyle= 'rgba(0, 0, 0, 0.5)';
    // 绘制图片
    this.LoadImg().then(() => {
      this.draw();
    });
  }

  public async LoadImg () {
    const img:any = new Image;
    return new Promise((resolve, reject) => {
      img.onload = () => {
        const iw:number = img.width;
        const ih:number = img.height;
        const cw:number = this.width * this.dpr;
        const ch:number = this.height * this.dpr;
        let sw:number = 0;
        let sh:number = 0;
  
        // 模拟background-size cover 的效果  让宽度设置为100%
        sw = cw;
        sh = sw * (ih / iw);
  
        // console.log(sh, ch);
        if (sh < ch) {
          sh = ch;
          sw = sh * (iw / ih);
        }
        this.options = {
          h: sh,
          i: img,
          w: sw,
          x: 0,
          y: 0,
        };
        resolve();
      };
      img.onerror = () => {
        reject();
        console.log('error');
      };
      img.src = require('../../assets/imgs/bg.jpg');
    });
  }

  public move (event: any) {
    this.points.push({
      time: Date.now(),
      x: event.clientX * this.dpr,
      y: event.clientY * this.dpr
    });
    if (this.points.length === 1) {
      this.LoadImg().then(() => {
        this.draw();
      });
    }
  }

  public draw () {
    let points = [...this.points];
    // 算出当前那些是需要显示的
    points = points.filter(point => {
      const age = Date.now() - point.time;
      return age < this.time;
    });
    this.points = points;
    this.drawLineCanvas();
    this.drawImageCanvas();
    requestAnimationFrame(() => {
      if (points.length > 0) {
        this.draw();
      }     
    });
  }

  public drawLineCanvas () {
    const minimumLineWidth = 40 * this.dpr;
    const maximumLineWidth = 120 * this.dpr;
    const lineWidthRange = maximumLineWidth - minimumLineWidth;
    const maximumSpeed = 30;
    const points = [...this.points];  
    const ctx = this.ctxImg;

    ctx.clearRect(0, 0, this.width * this.dpr, this.height * this.dpr);
    ctx.lineCap = 'round';
    ctx.shadowBlur = 30;
    ctx.shadowColor = '#000';
    
    for (let i = 1; i < points.length; i++) {
      const point = points[i];
      const previousPoint = points[i - 1];

      // Change line width based on speed
      const distance = this.getDistanceBetween(point, previousPoint);
      const speed = Math.max(0, Math.min(maximumSpeed, distance));
      const percentageLineWidth = (maximumSpeed - speed) / maximumSpeed;
      ctx.lineWidth = minimumLineWidth + percentageLineWidth * lineWidthRange;

      // Fade points as they age
      const age = Date.now() - point.time;
      const opacity = (this.time - age) / this.time;
      ctx.strokeStyle = `rgba(0, 0, 0, ${opacity})`;
      ctx.beginPath();
      ctx.moveTo(previousPoint.x, previousPoint.y);
      ctx.lineTo(point.x, point.y);
      ctx.stroke();
    }
  }

  public getDistanceBetween(a:any, b:any) {
    return Math.sqrt(Math.pow(a.x - b.x, 2) + Math.pow(a.y - b.y, 2));
  }

  public drawImageCanvas() {
    const ctx = this.ctx;
    ctx.globalCompositeOperation = 'source-over';
    this.drawImg(ctx);
    ctx.globalCompositeOperation = 'destination-in';
    ctx.drawImage(this.domImg, 0, 0);
  }

  public drawImg (ctx: any) {
    const obj:any = {...this.options};
    ctx.drawImage(obj.i, obj.x, obj.y, obj.w, obj.h);
  }
}

export default Draw;



